<template>
  <div class="v-show">
    <button @click="onToggleShow"></button>
    <div v-show="isShow"> </div>
  </div>
</template>

<script>
export default {
  name: 'vshow',
  data () {
    return {
      isShow: true
    }
  },
  methods: {
    onToggleShow () {
      this.isShow = !this.isShow
    }
  }
}
</script>
